.PlayerStatsList {
  font-family: "Permanent Marker";
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: space-between;
      align-items: space-between;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-direction: column;
      flex-direction: column;

  font-size: 0.5rem;
  width: 200px;
  padding: 0 1rem;
  border: 1px solid #fff;
  border-radius: 2px;
  color: #fff;
}

.PlayerStatsList h4 {
  width: 100%;
  margin-bottom: 4px;
}

.PlayerStatsList li {
  text-align: center;
}

.PlayerStatsList p {
  font-size: .35rem;
  font-family: Montserrat;
}

@media only screen and (max-width: 767px) {
  .PlayerStatsList {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    font-size: 0.65rem;    
    padding: .5rem;
  }

  .PlayerStatsList h4 {
    width: 100%;
    margin-bottom: 0;
  }

  .PlayerStatsList img {
    width: 50%;
    height: 50%;
  }

  .PlayerStatsList li {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: space-between;
        align-items: space-between;
    width: 13%;
  }

  .PlayerStatsList p {
    display: none;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .PlayerStatsList {
    font-size: .55rem;
  }

  .PlayerStatsList li p {
    font-size: .45rem;
  }
}


