@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat.ttf");
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 80%;
  margin: 15% auto;
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);  
}

.container header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.game-window {
  margin: 20px;
  display: -ms-flexbox;
  display: flex;
}

.arrow-keys {
  display: none;
  line-height: 0;
  color: #fff;
  width: 100%;
  text-align: center;
}

.arrow-keys button {
  color: #2f2f2f;
  font-size: 2.5rem;
  padding: .3rem .6rem;
}

.arrow-keys button:active {
  color: crimson;
}

@media only screen and (max-width: 767px) {
  .arrow-keys {
    display: block;
  }

  .game-window {
    display: block;
  }
  
  .container {
    -webkit-transform: scale(.8);
        -ms-transform: scale(.8);
            transform: scale(.8);
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .arrow-keys {
    display: block;
  }

  .container {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
  }
}


